import axios from 'axios'
import {baseUrl} from '../config.js'

export function getUser(){
    return axios.get(baseUrl+'/api/sessionInfo')
}

export function getDraft(id){
    return axios.get(baseUrl+'/api/taslak/'+id)
}

export function saveDraft(draftId,userId,data){
    return axios.put(baseUrl+'/api/taslak/'+draftId+'/'+userId,data)
}