import "./App.css";
import Editor from "./components/Editor.js";
import { useEffect, useState } from "react";
import Navbar from "react-bootstrap/Navbar";
import React from "react";
// import sendData from './functions/sendData.js'
import {getUser, getDraft, saveDraft} from "./functions/apiCalls";
import {baseUrl} from './config.js';

function App() {

  const [user,setUser]=useState(""); 
  const [document, updateDocument] = useState(""); 
  const [title, setTitle] = useState("");

  const draftId = new URLSearchParams(window.location.search).get(`draft`);

  useEffect(()=>{
    getUser().then(response => {
      if(response.data===""){
          window.location.href =baseUrl+"uyeGirisi";
      }
      else{
        setUser(response.data);
      }
    })
    getDraft(draftId).then(response => {    
      // console.log(response.data);
      updateDocument(JSON.parse(response.data.icerik));
      setTitle(response.data.baslik);
    })
  },[draftId]);

  

  useEffect(()=>{
    const interval = setInterval(() => {
      
      const data = {baslik:title,icerik:JSON.stringify(document)};
      console.log(data);
      saveDraft(draftId,user._id,data);
    }, 10000);
    return () => clearInterval(interval);
  }, [document, draftId, title, user._id]);

  return (
    <>
      <Navbar bg="dark" variant="dark">
        <Navbar.Brand href={baseUrl}>
          Ktszlk - {user.gercekAd}
        </Navbar.Brand>
      </Navbar>
      <div className="App">
        {title!=="" && <h1><input value={title} name="title" onChange={event => setTitle(event.target.value)}></input></h1>}
        {document!=="" && <Editor document={document} onChange={updateDocument} />}
        {document==="" && <h1>Ykleniyor...</h1>}
      </div>
    </>
  );
  }

  export default App;